body {
  box-sizing: border-box;
  color: #000;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica;
}

h1 {
  color: #000;
  margin-top: 100px;
}

.logo {
  width: 200px;
  margin-left: 20px;
}

.nav-bar {
  z-index: 10;
  background-color: #ff5200;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 30px;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  color: #fff !important;
}

.nav-bar > ul {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-bar ul > li {
  color: #fff;
  margin: 15px;
  list-style: none;
}

.nav-bar ul > li > a {
  color: #fff;
  font-weight: 600;
}

.search-container {
  justify-content: center;
  align-items: center;
  margin-top: 4em;
  display: flex;
}

.search-input {
  color: #5d5d5d;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  width: 450px;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
}

.search-btn {
  cursor: pointer;
  background-color: #df7f09;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  color: #fff !important;
  border: none !important;
}

.cart-btn {
  color: #000;
  cursor: pointer;
  background-color: #df7f09;
  border: 1px solid #d07200;
  border-radius: 3px;
  padding: 7px 16px;
}

.link {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.banner-container {
  background-color: #ff5200;
  justify-content: space-between;
  align-items: center;
  width: auto;
  display: flex;
  overflow-x: hidden;
}

#title {
  text-align: center;
  color: #fff;
  font-size: 3rem;
  font-weight: 900;
}

.banner-img {
  width: 20%;
  margin-top: 130px;
}

h2 {
  color: #df7f09;
  text-shadow: #5d5d5d;
  margin-left: 160px;
  font-size: 30px;
}

.card {
  color: #000;
  -o-transition: transform .3s ease, box-shadow .3s ease;
  border-radius: 15px;
  flex-direction: column;
  width: 280px;
  margin: 10px;
  padding: 15px;
  transition: transform .3s, box-shadow .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.card:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 12px #0003;
}

.card img {
  object-fit: cover;
  border-radius: 10px;
  width: 100%;
  max-height: 200px;
}

.card .content {
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
  line-height: 1.5;
}

.card:hover:after {
  content: "";
  z-index: 0;
  pointer-events: none;
  position: absolute;
  inset: 0;
}

button {
  color: #fff;
}

.card > img {
  aspect-ratio: 1;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  border-radius: 15px;
  width: 100%;
  height: 200px;
}

.restaurant-info {
  color: #000;
  text-transform: none;
  margin: 0;
  padding: 0;
}

.card-list {
